
import React from 'react';
import './nav_bar.css'

// import './pages/order.js'
// import { order as order_0 } from './pages/order.js'; 

var debug = 1; 

export async function NavigationBar(props) {

    let nav_bar_tabs = props.nav_bar_tabs; 
    let nav_bar_tab_select = props.nav_bar_tab_select; 
    let set_active_tab = props.set_active_tab; 

    return (

    <div className="nav-bar">

        <a href="https://innowings.engg.hku.hk/">
            <img
                src="assets/images/favicon_innowing_knowledge.png"
                alt="HKU Innovation Wing"
                className="nav-bar-logo"
            />
        </a>

        <div className="nav-bar-tabs">

            {nav_bar_tabs.map((tab, tab_id) => {

            let current_tab = tab[0];
            // console.log(current_tab);
            let is_active_tab = nav_bar_tab_select === tab_id; 
            let tab_class = is_active_tab ? 'nav-bar-tab active' : 'nav-bar-tab'; 

            return (
                <div 
                    key={`tab-button-${tab_id}`} 
                    className={tab_class}
                    onClick = {() => set_active_tab(tab_id)}
                >
                    {current_tab}
                </div>
            );

            })}
            
        </div>

    </div>

    );

}

async function retrieve_order(path) {
    const order_path = `${path}/order.js`; 
    if (debug === 1) {console.log(order_path); }

    try {
        // console.log(order_0); 
        console.log (123); 
        const module = await import(order_path);
        console.log (234); 
        const order = module.order;
        console.log (345); 
        console.log("Path: " + order_path);
        console.log("Retrieved order for the path " + path + ": " + order);
        return order;
    } catch (err) {
        console.log(err); 
        console.log("Path: " + order_path);
        console.log("The order for the path " + path + " not found.");
        return [];
    }
}

async function sorted_array(array, order) {
    if (!order) {return array; }
    let ordered_part = []
    let unordered_part = []
    let array_sorted = []
    array.forEach ((element) => {
        let element_key = typeof(element) === Array ? element[0] : element
        if (!order.includes(element_key)) {
            ordered_part.push(element); 
        } else {
            unordered_part.push(element); 
        }
    })
    ordered_part.map((element) => array_sorted.push(element)); 
    unordered_part.map((element) => array_sorted.push(element)); 
    return (array_sorted); 
}

const generate_nav_bar_tabs = async (current_browse_location) => {

    const page_files = (require.context('./pages', true, /^.*\.js$/)).keys().filter((key) => key.startsWith(current_browse_location))

    if (debug === 0) {console.log(page_files); }

    var nav_bar_tabs = []; 
    let first_level_tabs = []; 

    page_files.forEach((key) => {

        if (debug === 0) {console.log(key); }
        const file_path = key.replace(current_browse_location, ''); 
        const file_path_levels = file_path.split('/')

        if (file_path_levels.length < 2) {throw new Error ("Page Parsing Error. Current directory: " + current_browse_location)}
        const first_level = file_path_levels[1]
        const second_level = file_path_levels[2]

        if (debug === 0) {console.log(first_level); }
        if (!first_level_tabs.includes(first_level) && !first_level.endsWith('.js')) {
            nav_bar_tabs.push([first_level, []]); 
            first_level_tabs.push(first_level)
            if (debug === 0) {console.log(first_level); }
        }

        if (debug === 0) {console.log(second_level); }
        if (second_level && !second_level.endsWith('.js')) {
            try {
                nav_bar_tabs.forEach((first_level_pair) => {
                    if (first_level_pair[0] === first_level && !first_level_pair[1].includes(second_level)) {
                        first_level_pair[1].push(second_level); 
                        throw new Error ('break; ')
                    }
                })
            } catch {}
        } 

    }); 

    if (debug === 1) {console.log(nav_bar_tabs); }


    let first_level_tab_order = (await retrieve_order(`pages${current_browse_location}`))
    if (debug === 1) {console.log("first level order: " + first_level_tab_order); }
    nav_bar_tabs = (await sorted_array(nav_bar_tabs, first_level_tab_order)); 
    nav_bar_tabs.forEach(async (first_level_pair) => {
        let second_level_tab_order = (await retrieve_order(`pages${current_browse_location}/${first_level_pair[0]}`))
        if (debug === 1) {console.log("second level order: " + second_level_tab_order); }
        first_level_pair[1] = sorted_array(first_level_pair[1], second_level_tab_order); 
    })

    if (debug === 1) {console.log(nav_bar_tabs); }
    return (nav_bar_tabs); 

}

export default generate_nav_bar_tabs; 

